"use client";

import {
  createContext,
  useContext,
  useEffect,
  useState,
  ReactNode,
} from "react";
import {
  User as FirebaseUser,
  onAuthStateChanged,
  signOut as firebaseSignOut,
  RecaptchaVerifier,
  signInWithPhoneNumber,
  ConfirmationResult,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { doc, getDoc, setDoc, serverTimestamp } from "firebase/firestore";
import { auth, db } from "@/lib/firebase";
import { User } from "@/lib/firebase-types";

interface AuthContextType {
  firebaseUser: FirebaseUser | null;
  user: User | null;
  loading: boolean;
  isAdmin: boolean;

  // Phone auth
  sendOTP: (phoneNumber: string) => Promise<void>;
  verifyOTP: (otp: string) => Promise<void>;

  // Admin auth
  adminLogin: (email: string, password: string) => Promise<void>;

  // Sign out
  signOut: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const [firebaseUser, setFirebaseUser] = useState<FirebaseUser | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [confirmationResult, setConfirmationResult] =
    useState<ConfirmationResult | null>(null);
  const [recaptchaVerifier, setRecaptchaVerifier] =
    useState<RecaptchaVerifier | null>(null);

  // Listen to auth state changes
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
      setFirebaseUser(firebaseUser);

      if (firebaseUser) {
        // Fetch user data from Firestore
        const userDoc = await getDoc(doc(db, "users", firebaseUser.uid));
        if (userDoc.exists()) {
          setUser({ id: userDoc.id, ...userDoc.data() } as User);
        } else {
          setUser(null);
        }
      } else {
        setUser(null);
      }

      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  // Initialize reCAPTCHA verifier
  const initRecaptcha = async () => {
    if (typeof window === "undefined") return null;

    // Clear existing verifier if it exists
    if (recaptchaVerifier) {
      try {
        recaptchaVerifier.clear();
      } catch {
        // Ignore clear errors
      }
    }

    const verifier = new RecaptchaVerifier(auth, "recaptcha-container", {
      size: "invisible",
      callback: () => {
        // reCAPTCHA solved
      },
      "expired-callback": () => {
        // Reset reCAPTCHA when expired
        setRecaptchaVerifier(null);
      },
    });

    // Render the reCAPTCHA
    await verifier.render();
    setRecaptchaVerifier(verifier);
    return verifier;
  };

  // Send OTP to phone number
  const sendOTP = async (phoneNumber: string) => {
    // Format phone number for Nigeria if not already formatted
    let formattedPhone = phoneNumber;
    if (phoneNumber.startsWith("0")) {
      formattedPhone = "+234" + phoneNumber.slice(1);
    } else if (!phoneNumber.startsWith("+")) {
      formattedPhone = "+234" + phoneNumber;
    }

    // Initialize fresh reCAPTCHA verifier
    const verifier = await initRecaptcha();
    if (!verifier) throw new Error("reCAPTCHA not initialized");

    const result = await signInWithPhoneNumber(auth, formattedPhone, verifier);
    setConfirmationResult(result);
  };

  // Verify OTP
  const verifyOTP = async (otp: string) => {
    if (!confirmationResult) throw new Error("No OTP sent");

    const result = await confirmationResult.confirm(otp);
    const firebaseUser = result.user;

    // Check if user exists in Firestore
    const userDoc = await getDoc(doc(db, "users", firebaseUser.uid));

    if (!userDoc.exists()) {
      // Create new user
      const newUser: Omit<User, "id"> = {
        phone: firebaseUser.phoneNumber || "",
        createdAt: new Date(),
        createdBy: "self",
      };

      await setDoc(doc(db, "users", firebaseUser.uid), {
        ...newUser,
        createdAt: serverTimestamp(),
      });

      setUser({ id: firebaseUser.uid, ...newUser });
    } else {
      setUser({ id: userDoc.id, ...userDoc.data() } as User);
    }

    // Clear confirmation result
    setConfirmationResult(null);
  };

  // Admin login with email/password
  const adminLogin = async (email: string, password: string) => {
    const result = await signInWithEmailAndPassword(auth, email, password);

    // Check if user is admin
    const userDoc = await getDoc(doc(db, "users", result.user.uid));
    if (!userDoc.exists() || !userDoc.data()?.isAdmin) {
      await firebaseSignOut(auth);
      throw new Error("Unauthorized: Not an admin");
    }

    setUser({ id: userDoc.id, ...userDoc.data() } as User);
  };

  // Sign out
  const signOut = async () => {
    await firebaseSignOut(auth);
    setUser(null);
    setFirebaseUser(null);
  };

  const value: AuthContextType = {
    firebaseUser,
    user,
    loading,
    isAdmin: user?.isAdmin || false,
    sendOTP,
    verifyOTP,
    adminLogin,
    signOut,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
      {/* Hidden reCAPTCHA container */}
      <div id="recaptcha-container"></div>
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
}
